@use "base/variables" as v;
@use "base/mixins" as m;

.main-description {
  display: grid;
  grid-template-columns: 1fr;
  margin: 4rem 3rem;
  gap: 5rem;
  @include m.mobile {
    margin: 6rem 4rem;
  }
  @include m.tablet {
    margin-left: 7.5rem;
    margin-right: 7.5rem;
    margin-top: 5rem;
  }
  @include m.laptop {
    margin-top: 0;
    width: 70%;
    margin-left: auto;
    margin-right: auto;
  }
  @include m.desktop {
    margin: 15rem;
    margin-top: 2rem;
    margin-bottom: 10rem;
    width: 60%;
    margin-left: auto;
    margin-right: auto;
    // grid-template-columns: repeat(2, 1fr);
    // gap: 5rem;
  }
  @include m.desktopXL {
    margin-top: 10rem;
    margin-bottom: 15rem;
    width: 50%;
    margin-left: auto;
    margin-right: auto;
    // grid-template-columns: repeat(2, 1fr);
    // gap: 5rem;
  }
  .description-image {
    width: 100%;
    .img-container {
      width: 100%;
      img {
        width: 100%;
      }
    }
  }
  .description-text {
    h3 {
      color: v.$primaryBlueDark;
      font-family: v.$title;
      text-transform: uppercase;
      // font-size: 2rem;
      font-size: 1.7rem;
      @include m.tablet {
        font-size: 1.7rem;
      }
      @include m.laptop {
        font-size: 2.5rem;
      }
      @include m.desktop {
        font-size: 3rem;
      }
      @include m.desktopXL {
        font-size: 3.1rem;
      }
      text-align: center;
      margin-bottom: 3rem;
      span {
        @include m.tablet {
          display: block;
        }
      }
    }
    p {
      font-family: v.$text;
      color: v.$secondaryGray;
      // font-size: 1.5rem;
      @include m.laptop {
        font-size: 1.6rem;
      }
      @include m.desktop {
        font-size: 1.85rem;
      }
      @include m.desktopXL {
        font-size: 2rem;
      }
      text-align: center;
    }
  }
}
