@use "base/variables" as v;
@use "base/mixins" as m;

.main-services {
  position: relative;
  .main-services-grid {
    display: grid;
    grid-template-columns: 1fr;
    margin: 1rem;
    @include m.mobile {
      margin: 6rem 4rem 2rem 4rem;
      gap: 2rem;
    }
    @include m.tablet{
      grid-template-columns: repeat(2, 1fr);
      gap: 2rem;
      margin: 2rem auto 1rem;
      max-width: 50rem;
    }
    @include m.laptop {
      grid-template-columns: repeat(4, 1fr);
      gap: 1rem;
      margin: 2rem auto;
      margin-bottom: 10rem;
      max-width: 70rem;
    }
    @include m.desktop {
      gap: 4rem;
      margin: 5rem auto;
      margin-bottom: 10rem;
      max-width: 95rem;
    }
    @include m.desktopXL {
      gap: 5rem;
      margin: 8rem 18rem;
      max-width: unset;
    }
    .service {
      width: 15rem;
      height: 15rem;
      display: flex;
      justify-content: center;
      align-items: center;
      @include m.mobile {
        width: 20rem;
        height: 20rem;
      }
      @include m.laptop {
        width: 15rem;
        height: 15rem;
      }
      @include m.desktop {
        width: 20rem;
        height: 20rem;
        // height: 60rem;
      }
      @include m.desktopXL {
        // height: 60rem;
        width: 15vw;
        height: 15vw;
      }
      background-color: v.$serviceBackground;
      margin: auto;
      margin-bottom: 5rem;
      @include m.laptop {
        margin-bottom: auto;
      }
      position: relative;
      .img-container {
        img {
          width: 100%;
        }
      }
      .img-container {
        // margin-top: 3rem;
        padding-bottom: 1rem;
        width: 14rem;
        @include m.mobile {
          // height: 6rem;
          width: 20rem;
          padding-bottom: 1rem;
        }
        @include m.laptop {
          width: 80%;
          // margin-top: 10rem;
          // margin-bottom: 5rem;
          width: 24rem;
        }
        @include m.desktop {
          width: 30rem;
          // height: 8rem;
        }
        @include m.desktopXL {
          width: 50rem;
          // height: 8rem;
        }
      }
      .service-btn {
        position: absolute;
        bottom: -1.75rem;
        // font-family: v.$title;
        // font-size: 1.5rem;
        // background-color: v.$white;
        // color: v.$primaryBlue;
        // text-align: center;
        // border-radius: 3rem;
        margin: auto 5rem;
        // width: 10rem;
        // padding: 0.5rem 0;
        // @include m.mobile {
        //   bottom: -2rem;
        //   width: unset;
        //   padding: 1rem 2rem;
        // }
        // @include m.laptop {
        //   width: 80%;
        //   // padding: 1rem 5rem;
        //   // font-size: 1.75rem;
        //   border-radius: 3rem;
        // }
        // @include m.desktop {
        //   // padding: 1rem 8rem;
        //   // font-size: 2.5rem;
        //   border-radius: 3rem;
        // }
        // transition: all 0.5s;
        // &:hover {
        //   background-color: lighten($color: v.$blueBtn, $amount: 30);
        //   transition: all 0.5s;
        // }
        // button {
        //   color: v.$primaryBlueDark;
        // }
      }
      .service-btn-odd {
        // background-color: v.$pinkBtn;
        // color: darken($color: v.$pinkBtn, $amount: 20);
        // transition: all 0.5s;
        // button {
        //   color: v.$pinkTextBtn;
        // }
        // &:hover {
        //   background-color: darken($color: v.$pinkBtn, $amount: 5);
        //   transition: all 0.5s;
        // }
      }
    }
    .service:first-child {
      border-radius: 5rem 0 0 0;
    }
    .service:last-child {
      border-radius: 0 0 5rem 0;
    }
  }
  background-color: v.$blueGridDark;
  padding-top: 3rem;
  @include m.mobile {
    padding-bottom: 3rem;
  }
  @include m.laptop {
    background-color: transparent;
  }
  .service-bottom {
    bottom: 7rem;
    background-color: v.$blueGridDark;
    width: 100%;
    height: 16rem;
    position: absolute;
    z-index: -1;
    @include m.desktopXL {
      bottom: 2rem;
      height: 18rem;
    }
  }
}
