@use "base/variables" as v;
@use "base/mixins" as m;

.background-image {
  position: fixed;
  z-index: -1;
  margin-top: 0rem;
  img {
    width: 100%;
    height: 100%;
  }
}

.main-hero {
  position: relative;
  .hero-text {
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: v.$textNormal;
    color: v.$primaryBlueDark;
    font-size: 2rem;
    margin: auto;
    h2 {
      margin: 6rem 2rem;
      font-family: v.$title;
      @include m.mobile {
        margin: 6rem;
      }
      @include m.tablet {
        margin: 6rem 10rem;
      }
      @include m.laptop {
        margin-top: 12.5rem;
        margin-bottom: 7rem;
        margin-left: 15rem;
        margin-right: 15rem;
        font-size: 2.5rem;
      }
      @include m.desktop {
        margin: 15rem 20rem 10rem 20rem;
        font-size: 2.5rem;
      }
      @include m.desktopXL {
        margin-bottom: 10rem;
        margin: 20rem 30rem 10rem 30rem;
        font-size: 3.5rem;
      }
      text-align: center;
    }
  }
  .img-container {
    margin-top: -2rem;
    margin-bottom: 7rem;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 8rem;
    @include m.laptop {
      height: 12rem;
      margin-top: 0rem;
      margin-bottom: 10rem;
    }
    @include m.desktop {
      height: 15rem;
      margin-bottom: 15rem;
    }
    @include m.desktopXL {
      margin-bottom: 15rem;
    }
    img {
      height: 100%;
    }
  }
}

