.main-bg-image {
  display: none;
  position: absolute;
  z-index: -1;
  top: 0;
  width: 100%;
  @include m.tablet {
    display: block;
  }
  .img-container {
    width: 100%;
    height: auto;
    margin: 0;
    img {
      width: 100%;
      height: auto;
    }
  }
}
