@use "base/variables" as v;
@use "base/mixins" as m;

.main-form {
  margin-top: 5rem;
  @include m.laptop {
    margin: 4rem;
    width: 77%;
    margin-left: auto;
    margin-right: auto;
  }
  @include m.desktop {
    margin: 6rem;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    margin-top: 15rem;
  }
  @include m.desktopXL {
    margin: 6rem;
    margin-left: auto;
    margin-right: auto;
    margin-top: 20rem;
  }
}

.main-form-header {
  background-color: v.$primaryBlueDark;
  border-radius: none;
  height: auto;
  @include m.laptop {
    border-radius: 0 0 0 10rem;
    display: grid;
    grid-template-columns: 1fr;
  }
  @include m.desktop {
    height: 40rem;
    border-radius: 0 0 0 10rem;
    grid-template-columns: 4fr 5fr;
  }
  @include m.desktopXL {
    height: 40rem;
    border-radius: 0 0 0 10rem;
    grid-template-columns: 1fr 2fr;
  }
}

.form-title {
  display: flex;
  justify-content: center;
  align-items: center;
  color: v.$white;
  font-size: 1.75rem;
  text-align: center;
  padding: 3rem 2rem;
  @include m.laptop {
    font-size: 3rem;
    text-align: start;
    margin-left: 7rem;
  }
  @include m.desktop {
    padding-right: 10rem;
  }
  @include m.desktopXL {
    padding-right: 7rem;
  }
  font-family: v.$textNormal;
}

.main-form-body {
  display: grid;
  @include m.desktop {
    grid-template-columns: 2fr 3fr;
  }
  .form-description {
    margin-top: 3rem;
    @include m.tablet {
      margin: 5rem 0rem 0 0rem;
    }
    @include m.desktop {
      margin: 3rem 5rem 5rem 5rem;
    }
    @include m.desktopXL {
      margin: 5rem 7.5rem 10rem 7.5rem;
    }
    display: flex;
    flex-direction: column;
    .form-info {
      display: flex;
      justify-content: center;
      margin: 0 auto;
      @include m.tablet {
        margin: 1rem 15%;
        justify-content: center;
        margin-left: 25%;
      }
      @include m.desktop {
        margin: 1rem 0;
        justify-content: space-around;
      }
      @include m.desktopXL {
        margin: 1rem 0;
        justify-content: center;
      }
      .img-container {
        display: none;
        justify-content: center;
        width: 2.5rem;
        @include m.tablet {
          display: flex;
          width: 4rem;
        }
        @include m.laptop {
          width: 4.5rem;
        }
        @include m.desktop {
          width: 4.6rem;
        }
        padding: 0 0.5rem;
        padding-top: 0.5rem;
        img {
          width: 100%;
        }
      }
      .info-text {
        text-align: center;
        // margin-left: 1rem;
        font-size: 1.75rem;
        @include m.tablet {
          width: 25rem;
          text-align: left;
          margin-left: 2rem;
        }
        @include m.laptop {
          margin-left: 5rem;
          font-size: 2rem;
          margin-left: 2rem;
        }
        @include m.desktop {
          font-size: 2rem;
        }
        @include m.desktopXL {
          font-size: 2.5rem;
        }
        font-family: v.$textNormal;
        color: v.$secondaryGray;
      }
    }
    .form-info-multiple {
      align-items: flex-start;
    }
    .form-info-line {
      align-items: center;
    }
    .form-info-social-networks {
      margin: 2rem;
      display: flex;
      flex-direction: column;
      align-items: center;
      @include m.tablet {
        margin-top: 5rem;
      }
      @include m.laptop {
        flex-direction: row;
        justify-content: center;
      }
      .form-social-text {
        font-size: 2.5rem;
        @include m.laptop {
          margin-bottom: 2rem;
          margin-right: 3rem;
          font-size: 2.5rem;
          margin-bottom: unset;
        }
        @include m.desktopXL {
          font-size: 3rem;
        }
        font-family: v.$textNormal;
        color: v.$secondaryGray;
        p {
          @include m.laptop {
            margin: 0;
          }
        }
      }
      .form-social-icons {
        display: flex;
        margin-bottom: 3rem;
        @include m.laptop {
          margin-bottom: 0;
        }
        .img-container {
          height: 2.5rem;
          margin: auto 1rem;
          @include m.mobile {
            height: 2.75rem;
          }
          @include m.tablet {
            height: 3rem;
          }
          img {
            height: 100%;
          }
        }
      }
    }
  }
}

.home {
  .form-body {
    position: relative;
    .contact-form {
      @include m.tablet {
        padding: 2rem;
        margin: 3rem;
      }
      @include m.laptop {
        padding: 0;
        margin: 5rem;
      }
      @include m.desktop {
        padding: 0;
        margin: 0;
        margin-right: 4rem;
        position: absolute;
        top: -37rem;
      }
    }
    form {
      padding: 1rem;
      @include m.mobile {
        padding: 2rem;
      }
      @include m.laptop {
        padding: 1rem 5rem 5rem 5rem;
      }
    }
  }
}

.us {
  .form-body {
    // margin: 3rem 2rem;
    @include m.tablet {
      margin: 5rem 6rem;
    }
    @include m.desktop {
      width: 70%;
      margin-left: auto;
      margin-right: auto;
    }
    @include m.desktopXL {
      // margin: 5rem 20rem 8rem 20rem;
      width: 60%;
      margin-top: 5rem;
      margin-bottom: 20rem;
    }
  }
  form {
    padding: 2rem 3rem;
    @include m.tablet {
      padding: 2rem 4rem;
    }
    @include m.laptop {
      padding: 1rem 5rem 5rem 5rem;
    }
    @include m.desktop {
      padding: 1rem 10rem 5rem 10rem;
    }
  }
}

.form-body {
  .contact-form {
    padding: 0.5rem;
    background-color: v.$formBackground;
    .contact-form-title {
      font-family: v.$title;
      text-align: center;
      color: v.$primaryBlueDark;
      text-transform: uppercase;
      // font-family: v.$title;
      margin: 2rem auto 1rem auto;
      font-size: 2rem;
      // font-size: 2rem;
      @include m.tablet {
        font-size: 2.5rem;
      }
      @include m.laptop {
        margin: 1rem auto 2rem auto;
        font-size: 3rem;
        padding-top: 4rem;
      }
      @include m.desktop {
        font-size: 3rem;
        margin: 1rem auto 2rem auto;
      }
      @include m.desktopXL {
        padding-top: 0;
        padding-top: 3rem;
        font-size: 4rem;
      }
    }
  }
}

input,
select,
textarea {
  font-family: v.$textNarrow;
}

form {
  .aviso {
    padding-top: 0.5rem;
    font-size: 1.25rem;
    @include m.mobile {
      font-size: 1.4rem;
    }
    @include m.tablet {
      font-size: 1.5rem;
    }
    @include m.laptop {
      font-size: 1.75rem;
    }
    @include m.desktop {
      font-size: 1.8rem;
    }
    @include m.desktop {
      font-size: 2rem;
    }
  }
  .form-control,
  .form-select {
    background-color: v.$formInput;
    padding: 0.75rem;
    font-size: 1.25rem;
    @include m.mobile {
      font-size: 1.4rem;
    }
    @include m.tablet {
      font-size: 1.5rem;
    }
    @include m.laptop {
      padding: 1.5rem;
      margin: 0.5rem;
      font-size: 1.75rem;
    }
    @include m.desktop {
      padding: 0.75rem;
      font-size: 1.8rem;
    }
    @include m.desktopXL {
      padding: 1.5rem;
      font-size: 2rem;
    }
    // font-family: v.$textNormal;
    color: v.$secondaryGray;
    &:focus {
      border-color: v.$primaryBlue;
    }
  }
  label {
    font-family: v.$textNormal;
  }
  a {
    color: v.$primaryBlue;
    &:hover {
      color: v.$primaryBlueDark;
    }
  }
}

.form-check-input {
  width: 1.5rem;
  height: 1.5rem;
  margin-right: 1rem;
}

.form-control.is-invalid,
.was-validated .form-control:invalid {
  border-color: transparent;
  background-image: none;
  padding-right: 1rem;
}

.form-select.is-invalid:not([multiple]):not([size]),
.form-select.is-invalid:not([multiple])[size="1"],
.was-validated .form-select:invalid:not([multiple]):not([size]),
.was-validated .form-select:invalid:not([multiple])[size="1"],
.form-select.is-valid:not([multiple]):not([size]),
.form-select.is-valid:not([multiple])[size="1"],
.was-validated .form-select:valid:not([multiple]):not([size]),
.was-validated .form-select:valid:not([multiple])[size="1"] {
  padding-right: 1rem;
  border-color: transparent;
  background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16'%3e%3cpath fill='none' stroke='%23343a40' stroke-linecap='round' stroke-linejoin='round' stroke-width='2' d='M2 5l6 6 6-6'/%3e%3c/svg%3e");
}

.invalid-feedback {
  padding-left: 0.5rem;
  font-size: 1.15rem;
  font-family: v.$textNarrow;
  @include m.mobile {
    font-size: 1.25rem;
  }
  @include m.tablet {
    font-size: 1.3rem;
  }
  @include m.laptop {
    font-size: 1.5rem;
    margin-left: 1rem;
  }
  @include m.desktop {
    font-size: 1.5rem;
  }
  @include m.desktopXL {
    font-size: 1.75rem;
  }
}

.form-control.is-valid,
.was-validated .form-control:valid {
  border-color: transparent;
  padding-right: 1rem;
  background-image: none;
}

.form-check {
  margin-left: 0.5rem;
}

.form-check-input {
  margin-top: 0.5rem;
  @include m.tablet {
    margin-top: 0.9rem;
  }
  @include m.desktop {
    margin-top: 1.2rem;
  }
}

.form-check-input:checked {
  background-color: v.$primaryBlue;
  border-color: v.$primaryBlue;
}

.form-control.is-valid,
.was-validated .form-control:valid {
  border-color: v.$primaryBlue;
}

.form-check-input.is-valid:checked,
.was-validated .form-check-input:valid:checked,
.form-check-input.is-valid,
.was-validated .form-check-input:valid {
  background-color: v.$primaryBlue;
  border-color: v.$primaryBlueDark;
}

.col-md-12 {
  @include m.desktop {
    margin-bottom: 0.5rem;
  }
}

textarea {
  height: 17rem;
  @include m.desktopXL {
    height: 15rem;
  }
}
