@font-face {
  font-family: "CodeProBoldLC";
  src: url("../fonts/CodeProBoldLC.otf") format("opentype");
}
@font-face {
  font-family: "Gotham-Regular";
  src: url("../fonts/Gotham-Book.otf") format("opentype");
}
@font-face {
  font-family: "BaseticaPro-Med";
  src: url("../fonts/BaseticaPro-Med.otf") format("opentype");
}
@font-face {
  font-family: "CamingoDosProCd-SemiBold";
  src: url("../fonts/CamingoDosProCd-SemiBold.otf") format("opentype");
}
@font-face {
  font-family: "Gotham-Bold";
  src: url("../fonts/Gotham-Bold.otf") format("opentype");
}
