html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  line-height: 1.5;
  height: 100vh;
  display: flex;
  flex-direction: column;
}

main {
  flex: 1;
}

h2 {
  font-size: 1.25rem;
}
@media (min-width: 320px) {
  h2 {
    font-size: 1.5rem;
  }
}
@media (min-width: 481px) {
  h2 {
    font-size: 1.75rem;
  }
}
@media (min-width: 1025px) {
  h2 {
    font-size: 2rem;
  }
}

h3 {
  font-family: "Gotham-Bold";
  font-size: 1.5rem;
}
@media (min-width: 320px) {
  h3 {
    font-size: 1.75rem;
  }
}
@media (min-width: 481px) {
  h3 {
    font-size: 1.9rem;
  }
}
@media (min-width: 1025px) {
  h3 {
    font-size: 2.2rem;
  }
}
@media (min-width: 1201px) {
  h3 {
    font-size: 2.5rem;
  }
}

p {
  font-family: "CamingoDosProCd-SemiBold";
  font-size: 1.2rem;
}
@media (min-width: 320px) {
  p {
    font-size: 1.4rem;
  }
}
@media (min-width: 481px) {
  p {
    font-size: 1.5rem;
  }
}
@media (min-width: 1025px) {
  p {
    font-size: 1.7rem;
  }
}
@media (min-width: 1201px) {
  p {
    font-size: 1.9rem;
  }
}

.info-text a {
  font-family: "CamingoDosProCd-SemiBold";
  font-size: 1.2rem;
}
@media (min-width: 320px) {
  .info-text a {
    font-size: 1.4rem;
  }
}
@media (min-width: 481px) {
  .info-text a {
    font-size: 1.5rem;
  }
}
@media (min-width: 1025px) {
  .info-text a {
    font-size: 1.7rem;
  }
}
@media (min-width: 1201px) {
  .info-text a {
    font-size: 1.9rem;
  }
}

ul {
  padding-left: 0;
}
ul p {
  padding-left: 2rem;
  padding-right: 2rem;
}
@media (min-width: 768px) {
  ul {
    font-size: 2rem;
  }
}

li {
  list-style: disc;
  list-style-position: inside;
  font-size: 1.2rem;
}
@media (min-width: 320px) {
  li {
    font-size: 1.4rem;
  }
}
@media (min-width: 481px) {
  li {
    font-size: 1.5rem;
  }
}
@media (min-width: 1025px) {
  li {
    font-size: 1.7rem;
  }
}
@media (min-width: 1201px) {
  li {
    font-size: 1.9rem;
  }
}

form p {
  font-size: 1.1rem;
}
@media (min-width: 320px) {
  form p {
    font-size: 1.35rem;
  }
}
@media (min-width: 481px) {
  form p {
    font-size: 1.45rem;
  }
}
@media (min-width: 1025px) {
  form p {
    font-size: 1.65rem;
  }
}
@media (min-width: 1201px) {
  form p {
    font-size: 1.85rem;
  }
}

a {
  font-size: 1.3rem;
  text-decoration: none;
  color: unset;
}
@media (min-width: 481px) {
  a {
    font-size: 1.5rem;
  }
}
@media (min-width: 768px) {
  a {
    font-size: 1.6rem;
  }
}
@media (min-width: 1201px) {
  a {
    font-size: 1.8rem;
  }
}

.table {
  width: 90%;
  margin: auto;
}
@media (min-width: 481px) {
  .table {
    width: 70%;
  }
}
@media (min-width: 1025px) {
  .table {
    width: 60%;
  }
}
@media (min-width: 1201px) {
  .table {
    width: 50%;
  }
}

td {
  font-size: 1.1rem;
}
@media (min-width: 481px) {
  td {
    font-size: 1.25rem;
  }
}
@media (min-width: 1025px) {
  td {
    font-size: 1.5rem;
  }
}

tr {
  vertical-align: middle;
}

button,
.btn {
  font-size: 1.25rem;
}
@media (min-width: 320px) {
  button,
.btn {
    font-size: 1.5rem;
  }
}
.btn-send {
  background-color: #1a9bc4;
  color: #ffffff;
  text-transform: uppercase;
  font-family: "CamingoDosProCd-SemiBold";
  padding: 1rem 6rem;
  border-radius: 2rem;
  transition: all 0.5s;
}
.btn-send:hover {
  background-color: #1789ad;
  color: #ffffff;
  transition: all 0.5s;
}

.pink-btn {
  background-color: #ffccd9;
  transition: all 0.5s;
}
.pink-btn:hover {
  background-color: #ffb3c6;
  transition: all 0.5s;
}
.pink-btn button {
  color: #de6d8f;
}

.white-btn {
  background-color: #ffffff;
  transition: all 0.5s;
}
.white-btn:hover {
  background-color: #afdef2;
  transition: all 0.5s;
}
.white-btn button {
  color: #1a9bc4;
}

.blue-btn {
  background-color: #2BAFE5;
  transition: all 0.5s;
}
.blue-btn:hover {
  background-color: #1d89b8;
  transition: all 0.5s;
}
.blue-btn button {
  color: #ffffff;
}

.directory-btn {
  display: flex;
  justify-content: center;
  align-items: flex-end;
}
@media (min-width: 1201px) {
  .directory-btn {
    align-items: flex-start;
  }
}
.directory-btn a:has(button) {
  padding: 1rem 4rem;
}
@media (min-width: 768px) {
  .directory-btn a:has(button) {
    padding: 1rem 7rem;
  }
}

a:has(button) {
  padding: 1rem 1.5rem;
  border-radius: 3rem;
}
@media (min-width: 320px) {
  a:has(button) {
    padding: 1rem 3.5rem;
  }
}
@media (min-width: 768px) {
  a:has(button) {
    padding: 1rem 1.5rem;
  }
}
@media (min-width: 1025px) {
  a:has(button) {
    padding: 1rem 4rem;
  }
}
a:has(button) button {
  font-family: "BaseticaPro-Med";
  font-size: 1.15rem;
  border: none;
  background-color: transparent;
  text-align: center;
}
@media (min-width: 320px) {
  a:has(button) button {
    font-size: 1.45rem;
  }
}
@media (min-width: 481px) {
  a:has(button) button {
    font-size: 1.6rem;
  }
}
@media (min-width: 1025px) {
  a:has(button) button {
    font-size: 1.75rem;
  }
}
@media (min-width: 1201px) {
  a:has(button) button {
    font-size: 1.9rem;
  }
}

.form-select,
.form-control,
.invalid-feedback {
  font-size: 1rem;
}
@media (min-width: 320px) {
  .form-select,
.form-control,
.invalid-feedback {
    font-size: 1.25rem;
  }
}
@media (min-width: 768px) {
  .form-select,
.form-control,
.invalid-feedback {
    font-size: 1.75rem;
  }
}
label {
  font-size: 1.5rem;
}
@media (min-width: 320px) {
  label {
    font-size: 1.5rem;
  }
}
@media (min-width: 768px) {
  label {
    font-size: 2rem;
  }
}
.header-top {
  display: flex;
  align-items: center;
  justify-content: center;
  height: auto;
  background-color: #dfe7ec;
  font-family: "CamingoDosProCd-SemiBold";
}
@media (min-width: 320px) {
  .header-top {
    justify-content: space-between;
  }
}
.header-top p {
  font-size: 1.35rem;
  margin: 0;
  margin-bottom: 0.25rem;
  color: #747474;
  font-family: "CamingoDosProCd-SemiBold";
}
@media (min-width: 1025px) {
  .header-top p {
    font-size: 1.75rem;
    padding-left: 1rem;
    margin-bottom: 0.5rem;
  }
}
.header-top a {
  height: 100%;
}
.header-top a img {
  height: 100%;
}

.header-phone {
  display: none;
  align-items: center;
}
@media (min-width: 320px) {
  .header-phone {
    display: flex;
  }
}
.header-phone > * {
  margin: 1rem;
}
.header-phone .img-container {
  height: 2rem;
  padding-left: 1rem;
}
@media (min-width: 768px) {
  .header-phone .img-container {
    padding-left: 3rem;
  }
}
.header-phone .img-container img {
  height: 100%;
}

.header-social-media {
  display: flex;
  align-items: center;
}
@media (min-width: 481px) {
  .header-social-media {
    padding-right: 1rem;
  }
}
@media (min-width: 768px) {
  .header-social-media {
    padding-right: 2rem;
  }
}
.header-social-media .img-container {
  height: 2rem;
  margin: 1rem 1.5rem;
}
@media (min-width: 768px) {
  .header-social-media .img-container {
    margin: 3rem;
    margin-top: 1rem;
    margin-bottom: 1rem;
  }
}
.header-social-media .img-container img {
  height: 100%;
}
.header-social-media a {
  transition: transform 0.5s;
  display: flex;
  align-items: center;
}
.header-social-media a:hover {
  transform-origin: center center;
  transform: scale(1.2);
  transition: transform 0.5s;
}
.header-social-media a img {
  height: 100%;
}
.header-social-media .mail {
  height: 1.65rem;
}

.header-middle {
  height: 15rem;
  background-color: #1a9bc4;
  display: flex;
  align-items: center;
  justify-content: center;
}
@media (min-width: 768px) {
  .header-middle {
    height: 20rem;
  }
}
.header-middle .img-container {
  height: 7.5rem;
}
@media (min-width: 768px) {
  .header-middle .img-container {
    height: 12.5rem;
  }
}
.header-middle .img-container img {
  height: 100%;
}

.navbar {
  background-color: #dfe7ec;
  padding-bottom: 0;
  padding-top: 0;
}
.navbar .container-fluid {
  padding: 0;
}
.navbar .navbar-toggler {
  margin: 1rem auto;
  padding: 0.75rem;
}

.navbar-nav li {
  list-style: none;
}

@media (min-width: 768px) {
  .navbar-expand-md .navbar-collapse {
    display: block !important;
  }
}
.navbar-toggler:focus {
  box-shadow: none;
}

.header-bottom {
  font-family: "CodeProBoldLC";
  height: auto;
  background-color: #dfe7ec;
  display: flex;
  align-items: center;
}
@media (min-width: 768px) {
  .header-bottom {
    height: 7rem;
  }
}
@media (min-width: 1025px) {
  .header-bottom {
    height: 10rem;
  }
}
.header-bottom .navbar {
  background-color: #dfe7ec;
  padding: 0;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}
@media (min-width: 768px) {
  .header-bottom .navbar {
    flex-direction: row;
  }
}
.header-bottom .navbar .nav-item {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
}
@media (min-width: 768px) {
  .header-bottom .navbar .nav-item {
    width: 20%;
    height: 7rem;
  }
}
@media (min-width: 1025px) {
  .header-bottom .navbar .nav-item {
    height: 10rem;
  }
}
.header-bottom .navbar .nav-item .img-container {
  height: 3rem;
}
@media (min-width: 768px) {
  .header-bottom .navbar .nav-item .img-container {
    height: 3.35rem;
    display: none;
  }
}
@media (min-width: 1025px) {
  .header-bottom .navbar .nav-item .img-container {
    height: 4rem;
    display: block;
  }
}
@media (min-width: 1201px) {
  .header-bottom .navbar .nav-item .img-container {
    height: 5rem;
  }
}
.header-bottom .navbar .nav-item .img-container img {
  height: 100%;
}
.header-bottom .navbar .nav-item p {
  margin: 1.35rem;
  font-size: 1.3rem;
}
@media (min-width: 481px) {
  .header-bottom .navbar .nav-item p {
    font-size: 1.5rem;
  }
}
@media (min-width: 768px) {
  .header-bottom .navbar .nav-item p {
    font-size: 1.6rem;
    margin: 1rem;
    margin-right: 0;
  }
}
@media (min-width: 1025px) {
  .header-bottom .navbar .nav-item p {
    font-size: 2rem;
  }
}
@media (min-width: 1201px) {
  .header-bottom .navbar .nav-item p {
    font-size: 2.2rem;
    margin: 2rem;
  }
}
.header-bottom .navbar a {
  text-decoration: none;
  color: #747474;
}
.header-bottom .navbar a:hover {
  background-color: #cfdbe3;
}

.nav-link {
  display: flex;
  align-items: center;
}

.nav-item {
  width: 100%;
}

.navbar-light .navbar-nav .nav-link {
  width: 100%;
  height: 100%;
  padding: 0;
  display: flex;
  justify-content: center;
}

.header-bottom .navbar .nav-item p {
  font-size: 2rem;
}
@media (min-width: 768px) {
  .header-bottom .navbar .nav-item p {
    padding: 0;
    margin: 0;
  }
}
@media (min-width: 1025px) {
  .header-bottom .navbar .nav-item p {
    padding-left: 1rem;
  }
}

@media (min-width: 768px) {
  .header-bottom .navbar .dropdown p {
    padding-left: 0rem;
    padding-right: 0.5rem;
  }
}
@media (min-width: 1025px) {
  .header-bottom .navbar .dropdown p {
    padding-left: 1rem;
  }
}

.dropdown-toggle::after {
  display: none;
}
@media (min-width: 768px) {
  .dropdown-toggle::after {
    display: inline-block;
    margin-left: 0.255em;
    vertical-align: 0.255em;
    content: "";
    border-top: 0.3em solid;
    border-right: 0.3em solid transparent;
    border-bottom: 0;
    border-left: 0.3em solid transparent;
  }
}

@media (min-width: 768px) {
  .navbar-nav li:hover > ul.dropdown-menu {
    display: block;
    font-family: "CamingoDosProCd-SemiBold";
    min-width: 100%;
  }

  .dropdown-submenu {
    position: relative;
  }

  .dropdown-submenu > .dropdown-menu {
    top: 5rem;
    left: 100%;
    margin-top: -5rem;
  }

  .navbar-expand-md .navbar-nav .dropdown-menu {
    top: 7rem;
  }
}
@media (min-width: 768px) and (min-width: 1025px) {
  .navbar-expand-md .navbar-nav .dropdown-menu {
    top: 10rem;
  }
}
@media (min-width: 768px) {
  .dropdown-menu li {
    padding-bottom: 0.5rem;
  }
}
