.esthetics-elements .reveal {
  position: relative;
  opacity: 0;
}
.esthetics-elements .reveal.active {
  opacity: 1;
}
.esthetics-elements .active.fade-left {
  animation: fade-left 1s ease-in;
}
.esthetics-elements .active.fade-right {
  animation: fade-right 1s ease-in;
}
@keyframes fade-left {
  0% {
    transform: translateX(-100px);
    opacity: 0;
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}
@keyframes fade-right {
  0% {
    transform: translateX(100px);
    opacity: 0;
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}

