@use "base/variables" as v;
@use "base/mixins" as m;

.grid-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  @include m.tablet {
    margin-bottom: 10rem;
  }
  @include m.laptop {
    width: 85%;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 0;
  }
  @include m.desktop {
    width: 80%;
  }
}

.main-grid {
  width: 75%;
  display: grid;
  grid-template-columns: 1fr;
  @include m.tablet {
    width: 70%;
    grid-template-columns: repeat(6, 1fr);
    grid-template-rows: repeat(10, 1fr);
  }
  @include m.laptop {
    grid-template-columns: repeat(12, 1fr);
    grid-template-rows: repeat(5, 1fr);
    gap: 2rem;
    width: 90%;
  }
  & > * {
    color: v.$white;
    font-family: v.$textNormal;
    font-size: 2rem;
    text-align: center;
  }
  .img-container {
    height: 8rem;
    margin-top: 0rem;
    margin-bottom: 0rem;
    @include m.laptop {
      height: 10rem;
    }
    @include m.desktop {
      height: 11.5rem;
    }
    @include m.desktopXL {
      height: 14rem;
    }
    img {
      height: 100%;
    }
  }
  a:not(.grid-1, .grid-2, .grid-4, .grid-6) {
    &:hover {
      color: v.$white;
      background-color: darken(v.$blueGrid, 10%);
    }
  }
  a:not(.grid-3, .grid-5, .grid-7) {
    &:hover {
      color: v.$white;
      background-color: darken(v.$blueGridDark, 10%);
    }
  }
  @include m.tablet {
    a:not(.grid-1, .grid-4, .grid-5) {
      &:hover {
        color: v.$white;
        background-color: darken(v.$blueGrid, 10%);
      }
    }
    a:not(.grid-2, .grid-3, .grid-6, .grid-7, .grid-8) {
      &:hover {
        color: v.$white;
        background-color: darken(v.$blueGridDark, 10%);
      }
    }
  }
  p {
    font-size: 1.65rem;
    @include m.tablet {
      font-size: 1.75rem;
    }
    @include m.laptop {
      font-size: 1.9rem;
    }
    @include m.desktop{
      font-size: 2.1rem;
    }
    @include m.desktopXL{
      font-size: 2.4rem;
    }
  }
}

.grid-1 {
  font-family: v.$titleBold;
  @include m.tablet {
    grid-area: 1 / 1 / 2 / 7;
    border-radius: 4rem 0 0 0;
  }
  @include m.laptop {
    grid-area: 1 / 1 / 2 / 5;
    border-radius: 4rem 0 0 0;
  }
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: v.$blueGridDark;
  border-radius: 4rem 4rem 0 0;
}
.grid-2 {
  @include m.tablet {
    grid-area: 2 / 1 / 5 / 4;
    background-color: v.$blueGrid;
  }
  @include m.laptop {
    grid-area: 2 / 1 / 4 / 5;
  }
  background-color: v.$blueGridDark;
}
.grid-3 {
  @include m.tablet {
    grid-area: 2 / 4 / 5 / 7;
  }
  @include m.laptop {
    grid-area: 1 / 5 / 4 / 9;
  }
  background-color: v.$blueGrid;
}
.grid-4 {
  @include m.tablet {
    grid-area: 5 / 1 / 8 / 4;
  }
  @include m.laptop {
    grid-area: 1 / 9 / 4 / 13;
  }
  background-color: v.$blueGridDark;
}
.grid-5 {
  @include m.tablet {
    grid-area: 5 / 4 / 8 / 7;
    background-color: v.$blueGridDark;
  }
  @include m.laptop {
    grid-area: 4 / 1 / 6 / 4;
  }
  background-color: v.$blueGrid;
}
.grid-6 {
  @include m.tablet {
    grid-area: 8 / 1 / 11 / 3;
    border-radius: 0;
    background-color: v.$blueGrid;
  }
  @include m.laptop {
    grid-area: 4 / 4 / 6 / 7;
  }
  background-color: v.$blueGridDark;
}
.grid-7 {
  @include m.tablet {
    grid-area: 8 / 3 / 11 / 5;
  }
  @include m.laptop {
    grid-area: 4 / 7 / 6 / 10;
  }
  background-color: v.$blueGrid;
}
.grid-8 {
  padding-top: 2rem;
  padding-bottom: 2rem;
  @include m.tablet {
    grid-area: 8 / 5 / 11 / 7;
    border-radius: 0 0 4rem 0;
    background-color: v.$blueGrid;
  }
  @include m.laptop {
    grid-area: 4 / 10 / 6 / 13;
    border-radius: 0 0 4rem 0;
  }
  background-color: v.$blueGridDark;
  border-radius: 0 0 4rem 4rem;
}

.grid-1,
.grid-2,
.grid-3,
.grid-4,
.grid-5,
.grid-6,
.grid-7 {
  display: flex;
  padding: 1.5rem 0 1.5rem 0;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.grid-1 {
  padding-bottom: 0;
}

.grid-2,
.grid-3,
.grid-4,
.grid-5,
.grid-6,
.grid-7 {
  font-family: v.$title;
}
