@use "variables" as v;
@use "base/mixins" as m;

html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  line-height: 1.5;
  height: 100vh;
  display: flex;
  flex-direction: column;
  // margin: 0;
}

main {
  flex: 1;
}

h2 {
  font-size: 1.25rem;
  @include m.mobile {
    font-size: 1.5rem;
  }
  @include m.tablet {
    font-size: 1.75rem;
  }
  @include m.desktop {
    font-size: 2rem;
  }
}

h3 {
  font-family: v.$title;
  font-size: 1.5rem;
  @include m.mobile {
    font-size: 1.75rem;
  }
  @include m.tablet {
    font-size: 1.9rem;
  }
  @include m.desktop {
    font-size: 2.2rem;
  }
  @include m.desktopXL {
    font-size: 2.5rem;
  }
}

p {
  font-family: v.$textNarrow;
  font-size: 1.2rem;
  @include m.mobile {
    font-size: 1.4rem;
  }
  @include m.tablet {
    font-size: 1.5rem;
  }
  @include m.desktop {
    font-size: 1.7rem;
  }
  @include m.desktopXL {
    font-size: 1.9rem;
  }
}

.info-text {
  a {
    font-family: v.$textNarrow;
    font-size: 1.2rem;
    @include m.mobile {
      font-size: 1.4rem;
    }
    @include m.tablet {
      font-size: 1.5rem;
    }
    @include m.desktop {
      font-size: 1.7rem;
    }
    @include m.desktopXL {
      font-size: 1.9rem;
    }
  }
}

ul {
  padding-left: 0;
  p {
    padding-left: 2rem;
    padding-right: 2rem;
  }
  @include m.laptop {
    font-size: 2rem;
  }
}

li {
  list-style: disc;
  list-style-position: inside;
  font-size: 1.2rem;
  @include m.mobile {
    font-size: 1.4rem;
  }
  @include m.tablet {
    font-size: 1.5rem;
  }
  @include m.desktop {
    font-size: 1.7rem;
  }
  @include m.desktopXL {
    font-size: 1.9rem;
  }
}

form {
  p {
    font-size: 1.1rem;
    @include m.mobile {
      font-size: 1.35rem;
    }
    @include m.tablet {
      font-size: 1.45rem;
    }
    @include m.desktop {
      font-size: 1.65rem;
    }
    @include m.desktopXL {
      font-size: 1.85rem;
    }
  }
}

a {
  font-size: 1.3rem;
  text-decoration: none;
  color: unset;
  @include m.tablet {
    font-size: 1.5rem;
  }
  @include m.laptop {
    font-size: 1.6rem;
  }
  @include m.desktopXL {
    font-size: 1.8rem;
  }
}

.table {
  width: 90%;
  margin: auto;
  @include m.tablet {
    width: 70%;
  }
  @include m.desktop {
    width: 60%;
  }
  @include m.desktopXL {
    width: 50%;
  }
}

td {
  font-size: 1.1rem;
  @include m.tablet {
    font-size: 1.25rem;
  }
  @include m.desktop {
    font-size: 1.5rem;
  }
}

tr {
  vertical-align: middle;
}

button,
.btn {
  font-size: 1.25rem;
  @include m.mobile {
    font-size: 1.5rem;
  }
  @include m.laptop {
    // font-size: 1.5rem;
  }
  @include m.desktop {
    // font-size: 1.5rem;
  }
  @include m.desktopXL {
    // font-size: 1.5rem;
  }
}

.btn-send {
  background-color: v.$primaryBlueDark;
  color: v.$white;
  text-transform: uppercase;
  font-family: v.$textNarrow;
  padding: 1rem 6rem;
  border-radius: 2rem;
  transition: all 0.5s;
  &:hover {
    background-color: darken($color: v.$primaryBlueDark, $amount: 5);
    color: v.$white;
    transition: all 0.5s;
  }
}

.pink-btn {
  background-color: v.$pinkBtn;
  transition: all 0.5s;
  &:hover {
    background-color: darken($color: v.$pinkBtn, $amount: 5);
    transition: all 0.5s;
  }
  button {
    color: v.$pinkTextBtn;
  }
}

.white-btn {
  background-color: v.$white;
  transition: all 0.5s;
  &:hover {
    background-color: lighten($color: v.$blueBtn, $amount: 30);
    transition: all 0.5s;
  }
  button {
    color: v.$primaryBlueDark;
  }
}

.blue-btn {
  background-color: v.$primaryBlue;
  transition: all 0.5s;
  &:hover {
    background-color: darken($color: v.$blueBtn, $amount: 10);
    transition: all 0.5s;
  }
  button {
    color: v.$white;
  }
}

.directory-btn {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  @include m.desktopXL {
    align-items: flex-start;
  }
  a:has(button) {
    padding: 1rem 4rem;
    @include m.laptop {
      padding: 1rem 7rem;
    }
  }
}

a:has(button) {
  padding: 1rem 1.5rem;
  border-radius: 3rem;
  @include m.mobile {
    padding: 1rem 3.5rem;
  }
  @include m.laptop {
    padding: 1rem 1.5rem;
  }
  @include m.desktop {
    padding: 1rem 4rem;
  }
  button {
    font-family: v.$text;
    font-size: 1.15rem;
    border: none;
    background-color: transparent;
    text-align: center;
    @include m.mobile {
      font-size: 1.45rem;
    }
    @include m.tablet {
      font-size: 1.6rem;
    }
    @include m.desktop {
      font-size: 1.75rem;
    }
    @include m.desktopXL {
      font-size: 1.9rem;
    }
  }
}

.form-select,
.form-control,
.invalid-feedback {
  font-size: 1rem;
  @include m.mobile {
    font-size: 1.25rem;
  }
  @include m.laptop {
    font-size: 1.75rem;
  }
  @include m.desktop {
    // font-size: 0.75rem;
  }
  @include m.desktopXL {
    // font-size: 1.5rem;
  }
}

label {
  font-size: 1.5rem;
  @include m.mobile {
    font-size: 1.5rem;
  }
  @include m.laptop {
    font-size: 2rem;
  }
  @include m.desktop {
    // font-size: 0.75rem;
  }
  @include m.desktopXL {
    // font-size: 1.5rem;
  }
}