@use "base/variables" as v;
@use "base/mixins" as m;

.header-top {
  display: flex;
  align-items: center;
  justify-content: center;
  @include m.mobile {
    justify-content: space-between;
  }
  height: auto;
  background-color: v.$grayBackground;
  font-family: v.$textNarrow;
  p {
    font-size: 1.35rem;
    margin: 0;
    margin-bottom: 0.25rem;
    color: v.$secondaryGray;
    font-family: v.$textNarrow;
    @include m.desktop {
      font-size: 1.75rem;
      padding-left: 1rem;
      margin-bottom: 0.5rem;
    }
  }
  //
  a {
    height: 100%;
    img {
      height: 100%;
    }
  }
  //
}

.header-phone {
  display: none;
  align-items: center;
  @include m.mobile {
    display: flex;
  }
  & > * {
    margin: 1rem;
  }
  .img-container {
    height: 2rem;
    padding-left: 1rem;
    @include m.laptop {
      padding-left: 3rem;
    }
    img {
      height: 100%;
    }
  }
}

.header-social-media {
  display: flex;
  align-items: center;
  @include m.tablet {
    padding-right: 1rem;
  }
  @include m.laptop {
    padding-right: 2rem;
  }
  .img-container {
    height: 2rem;
    margin: 1rem 1.5rem;
    @include m.laptop {
      //
      margin: 3rem;
      margin-top: 1rem;
      margin-bottom: 1rem;
      //
    }
    // &:last-child {
    //   padding-right: 1rem;
    //   @include m.laptop {
    //     padding-right: 2rem;
    //   }
    // }
    img {
      height: 100%;
    }
  }
  a {
    transition: transform 0.5s;
    display: flex;
    align-items: center;
    &:hover {
      transform-origin: center center;
      transform: scale(1.2);
      transition: transform 0.5s;
    }
    img {
      height: 100%;
    }
  }
  .mail {
    height: 1.65rem;
  }
}

.header-middle {
  height: 15rem;
  @include m.laptop {
    height: 20rem;
  }
  background-color: v.$primaryBlueDark;
  display: flex;
  align-items: center;
  justify-content: center;
  .img-container {
    height: 7.5rem;
    @include m.laptop {
      height: 12.5rem;
    }
    img {
      height: 100%;
    }
  }
}

.navbar {
  background-color: v.$grayBackground;
  padding-bottom: 0;
  padding-top: 0;
  .container-fluid {
    padding: 0;
  }
  .navbar-toggler {
    margin: 1rem auto;
    padding: 0.75rem;
  }
}

.navbar-nav {
  li {
    list-style: none;
  }
}

@include m.laptop {
  .navbar-expand-md .navbar-collapse {
    display: block !important;
  }
}
.navbar-toggler:focus {
  box-shadow: none;
}

.header-bottom {
  font-family: v.$titleBold;
  height: auto;
  @include m.laptop {
    height: 7rem;
  }
  @include m.desktop {
    height: 10rem;
  }
  background-color: v.$grayBackground;
  display: flex;
  align-items: center;
  .navbar {
    background-color: v.$grayBackground;
    padding: 0;
    width: 100%;
    display: flex;
    flex-direction: column;
    @include m.laptop {
      flex-direction: row;
    }
    justify-content: space-evenly;
    align-items: center;
    .nav-item {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 100%;
      @include m.laptop {
        width: 20%;
        height: 7rem;
      }
      @include m.desktop {
        height: 10rem;
      }
      .img-container {
        height: 3rem;
        @include m.laptop {
          height: 3.35rem;
          display: none;
        }
        @include m.desktop {
          height: 4rem;
          display: block;
        }
        @include m.desktopXL {
          height: 5rem;
        }
        img {
          height: 100%;
        }
      }
      p {
        // font-family: v.$textNormal;
        // font-size: 1.25rem;
        margin: 1.35rem;
        font-size: 1.3rem;
        @include m.tablet {
          font-size: 1.5rem;
        }
        @include m.laptop {
          font-size: 1.6rem;
          margin: 1rem;
          margin-right: 0;
        }
        @include m.desktop {
          font-size: 2rem;
        }
        @include m.desktopXL {
          font-size: 2.2rem;
          margin: 2rem;
        }
      }
    }
    a {
      text-decoration: none;
      color: v.$secondaryGray;
      &:hover {
        background-color: darken($color: v.$grayBackground, $amount: 5);
      }
    }
  }
}

.nav-link {
  display: flex;
  align-items: center;
}

.nav-item {
  width: 100%;
}

.navbar-light .navbar-nav .nav-link {
  width: 100%;
  height: 100%;
  padding: 0;
  display: flex;
  justify-content: center;
}

.header-bottom .navbar .nav-item p {
  font-size: 2rem;
  @include m.laptop {
    padding: 0;
    margin: 0;
  }
  @include m.desktop {
    padding-left: 1rem;
  }
}

.header-bottom .navbar .dropdown p {
  @include m.laptop {
    padding-left: 0rem;
    padding-right: 0.5rem;
  }
  @include m.desktop {
    padding-left: 1rem;
  }
}

.dropdown-toggle::after {
  display: none;
  @include m.laptop {
    display: inline-block;
    margin-left: 0.255em;
    vertical-align: 0.255em;
    content: "";
    border-top: 0.3em solid;
    border-right: 0.3em solid transparent;
    border-bottom: 0;
    border-left: 0.3em solid transparent;
  }
}

@include m.laptop {
  .navbar-nav li:hover > ul.dropdown-menu {
    display: block;
    font-family: v.$textNarrow;
    min-width: 100%;
  }
  .dropdown-submenu {
    position: relative;
  }
  .dropdown-submenu > .dropdown-menu {
    top: 5rem;
    left: 100%;
    margin-top: -5rem;
  }

  .navbar-expand-md .navbar-nav .dropdown-menu {
    top: 7rem;
    @include m.desktop {
      top: 10rem;
    }
  }

  .dropdown-menu li {
    padding-bottom: 0.5rem;
  }
}

/* rotate caret on hover */
.dropdown-menu > li > a:hover:after {
  text-decoration: underline;
  transform: rotate(-90deg);
}

