@use "base/variables" as v;
@use "base/mixins" as m;

.services-procedures {
  background-color: v.$grayBackground;
  // margin: 3rem;
  font-family: v.$textNormal;
  padding: 3rem;
  text-align: center;
  @include m.mobile {
    margin-bottom: 5rem;
  }
  @include m.tablet {
    width: 80%;
    margin: 3rem auto;
    text-align: left;
  }
  @include m.desktop {
    margin-bottom: 10rem;
    max-width: 120rem;
  }
  p,
  li {
    // font-size: 1.5rem;
    font-family: v.$textNormal;
  }
  ol {
    padding-left: 0;
    @include m.tablet {
      padding-left: unset;
    }
    li {
      list-style: decimal;
      list-style-position: inside;
    }
  }
  ul {
    li {
      color: v.$primaryBlue;
      font-family: v.$titleBold;
    }
    p {
      color: v.$secondaryGray;
      font-family: v.$text;
      margin-left: -2rem;
      margin-right: -1rem;
    }
  }
  ol {
    li {
      color: v.$secondaryGray;
    }
  }
}
