@use "base/variables" as v;
@use "base/mixins" as m;

.lab-title {
  position: relative;
  display: flex;
  justify-content: center;
  h2 {
    font-family: v.$title;
    position: absolute;
    text-transform: uppercase;
    color: v.$white;
    font-size: 2.5rem;
    top: -1.5rem;
    text-align: center;
    @include m.tablet {
      font-size: 4rem;
      top: -2.5rem;
    }
    @include m.desktop {
      font-size: 8rem;
      top: -5rem;
    }
    @include m.desktopXL {
      font-size: 10rem;
      top: -6rem;
    }
  }
}

.lab-description {
  .lab-description-grid {
    display: grid;
    gap: 1rem;
    @include m.laptop {
      grid-template-columns: 1fr 3fr;
      gap: 3rem;
    }
    .lab-description-logo {
      display: flex;
      align-items: center;
      width: 17rem;
      margin: auto;
      margin-bottom: 2rem;
      @include m.tablet {
        width: 30rem;
        margin-bottom: auto;
      }
      img {
        width: 100%;
      }
    }
    .lab-description-text {
      display: flex;
      align-items: center;
      font-family: v.$text;
      padding: 1rem;
      @include m.tablet {
        padding: 2rem;
      }
      p {
        font-family: v.$text;
        text-align: center;
        // font-size: 2rem;
        line-height: 1.5;
        @include m.mobile {
          line-height: unset;
        }
        @include m.tablet {
          text-align: justify;
        }
      }
    }
  }
  padding: 5rem 2rem;
  @include m.mobile {
    padding: 7rem 5rem;
  }
  @include m.tablet {
    padding: 7rem 7rem;
  }
  @include m.desktop {
    padding: 12rem 15rem;
  }
  @include m.desktopXL {
    padding: 20rem 15rem;
  }
  background-color: v.$primaryBlueDark;
  p {
    color: v.$white;
    margin: 0;
    font-size: 1.25rem;
    line-height: 3rem;
    @include m.mobile {
      font-size: 1.4rem;
    }
    @include m.tablet {
      font-size: 1.7rem;
    }
    @include m.desktop {
      font-size: 2rem;
      line-height: 5rem;
    }
    @include m.desktopXL {
      font-size: 2.5rem;
      line-height: 5rem;
    }
    text-align: center;
  }
}

.lab-procedures {
  background-color: v.$grayBackground;
  margin: 0 0 3rem 0;
  padding: 3rem;
  text-align: center;
  @include m.tablet {
    margin: 3rem;
    text-align: left;
  }
  @include m.laptop {
    width: 85%;
    margin-left: auto;
    margin-right: auto;
    margin-top: 7.5rem;
  }
  @include m.desktop {
    width: 70%;
    margin: 10rem auto 3rem auto;
  }
  @include m.desktopXL {
    // max-width: 120rem;
    margin: 15rem auto 3rem auto;
  }
  p,
  li {
    // font-size: 1.5rem;
    font-family: v.$textNormal;
  }
  li {
    color: v.$primaryBlue;
    font-family: v.$titleBold;
    padding-bottom: 1rem;
    @include m.laptop {
      font-size: 1.75rem;
    }
    @include m.desktop {
      font-size: 2rem;
    }
    @include m.desktopXL {
      font-size: 2.5rem;
    }
  }
  ul {
    p {
      margin-left: -2rem;
      margin-right: -1rem;
      font-family: v.$text;
      @include m.laptop {
        font-size: 1.65rem;
      }
      @include m.desktop {
        font-size: 1.75rem;
      }
      @include m.desktopXL {
        font-size: 2rem;
      }
    }
  }
}

.lab-packages {
  position: relative;
  .lab-packages-grid {
    padding: 2rem;
    display: grid;
    grid-template-columns: 1fr;
    .lab-packages-img {
      width: 100%;
      @include m.mobile {
        width: 80%;
        margin: auto;
      }
      @include m.tablet {
        width: 27rem;
        margin: auto;
        padding-left: 2rem;
        padding-right: 2rem;
      }
      @include m.desktop {
        width: 28rem;
      }
      img {
        width: 100%;
      }
    }
    @include m.tablet {
      grid-template-columns: 1fr 2fr;
      padding-left: 5%;
      padding-right: 5%;
    }
    @include m.laptop {
      width: 70%;
      margin-left: auto;
      margin-right: auto;
    }
    @include m.desktop {
      width: 65%;
      margin-left: auto;
      margin-right: auto;
    }
    @include m.desktopXL {
      width: 60%;
      margin-left: auto;
      margin-right: auto;
    }
  }
  .lab-packages-text {
    background-color: transparent;
    color: v.$white;
    text-align: center;
    text-transform: uppercase;
    margin: 4rem auto;
    margin-bottom: 5rem;
    a {
      font-family: v.$titleBold;
      font-size: 2rem;
      text-align: center;
      @include m.tablet {
        font-size: 2.5rem;
      }
      @include m.laptop {
        font-size: 2.75rem;
      }
      @include m.desktop {
        font-size: 3.5rem;
      }
      @include m.desktopXL {
        font-size: 4rem;
      }
      transition: color 0.5s;
      &:hover {
        transition: color 0.5s;
        color: black;
      }
    }
    @include m.tablet {
      display: flex;
      justify-content: center;
      align-items: center;
    }
  }
  .lab-packages-bg {
    background-color: v.$primaryBlue;
    height: 30rem;
    width: 100%;
    position: absolute;
    bottom: 3rem;
    z-index: -1;
    @include m.tablet {
      height: 25rem;
      bottom: 2.75rem;
    }
    @include m.laptop {
      left: 15%;
      width: 70%;
      border-radius: 0 0 5rem 0;
    }
    @include m.desktop {
      width: 65%;
      left: 17.5%;
      bottom: 3.5rem;
    }
    @include m.desktopXL {
      width: 55%;
      left: 22.5%;
      bottom: 3.2rem;
    }
  }
  @include m.laptop {
    margin: 7rem auto 5rem auto;
  }
  @include m.desktop {
    margin: 12rem auto 14rem auto;
  }
}

