  height: 10rem;
  @include m.tablet {
    height: 15rem;
    margin-top: 5rem;
  }
  @include m.laptop {
    height: 20rem;
  }
  @include m.desktop {
    height: 30rem;
  }
  .lab-procedures-list-head {
