td {
  font-family: v.$text;
  @include m.tablet {
    font-size: 1.35rem;
  }
  @include m.laptop {
    font-size: 1.5rem;
  }
  @include m.desktop {
    font-size: 1.75rem;
  }
}

// Pagination

.lab-pagination {
  max-width: 120rem;
  margin: auto;
  margin-bottom: 5rem;
  @include m.tablet {
    margin-bottom: 10rem;
  }
  .panel {
    display: flex;
    justify-content: flex-end;
    margin: 2rem;
    font-size: 1.25rem;
    @include m.mobile {
      margin: 5rem 2rem 3rem 2rem;
    }
    @include m.tablet {
      width: 70%;
      margin: 0 auto 3rem auto;
    }
    @include m.desktop {
      width: 60%;
    }
    @include m.desktopXL {
      width: 50%;
    }
  }
  .input-group {
    display: flex;
    justify-content: center;
    @include m.mobile {
      flex-wrap: nowrap;
    }
    label {
      padding: 1rem 2rem 1rem 1rem;
      font-family: v.$text;
    }
    input {
      border: 1px solid v.$primaryBlueDark;
      padding: 1rem;
      width: 100%;
      margin-top: 0.75rem;
      height: 3rem;
      @include m.mobile {
        padding: 0.5rem;
      }
      @include m.tablet {
        width: 20rem;
      }
    }
  }
  .lab-column {
    padding-left: 0;
    vertical-align: sub;
    @include m.tablet {
      border-left: 2px solid v.$primaryBlueDark;
    }
  }
  tr {
    border-style: hidden;
    height: 4rem;
    vertical-align: middle;
  }
  .lab-price {
    border-style: hidden;
    display: flex;
    justify-content: space-between;
    @include m.tablet {
      justify-content: space-between;
      width: 7.5rem;
    }
  }
  .paginate_controls {
    display: flex;
    justify-content: center;
    margin: 3rem;
    input[type="button"]:not(.paginate_control_prev, .paginate_control_next) {
      border-radius: 50%;
      width: 2.5rem;
      height: 2.5rem;
      margin: 0.5rem 0.5rem 0.5rem 0.5rem;
      font-size: 1.2rem;
      border: 2px solid v.$primaryBlueDark;
      &:hover {
        background-color: v.$primaryBlueDark;
        color: v.$white;
      }
      @include m.tablet {
        width: 3.5rem;
        height: 3.5rem;
        margin-top: 1rem;
        font-size: 1.3rem;
      }
    }
    input[type="button"]:not(.active) {
      background-color: transparent;
    }
    .active {
      background-color: v.$primaryBlueDark;
      color: v.$white;
    }
    .paginate_control_prev,
    .paginate_control_next {
      background-color: transparent;
      border: none;
      font-size: 2rem;
      color: v.$primaryBlueDark;
      @include m.tablet {
        font-size: 4rem;
      }
    }
  }
  .paginate-controls {
    font-family: v.$text;
  }
}

.lab-pack {
  .lab-pack-price {
    position: absolute;
    background-color: v.$grayBackground;
    color: v.$primaryBlueDark;
    width: 7rem;
    height: 7rem;
    right: -1rem;
    bottom: -3rem;
    display: flex;
    justify-content: center;
    // padding-top: 3rem;
    align-items: center;
    border-radius: 50% 50% 0 0;
    @include m.mobile {
      width: 7.5rem;
      height: 7.5rem;
    }
    @include m.desktopXL {
      width: 8rem;
      height: 8rem;
    }
    p {
      margin: 0;
      font-size: 1.5rem;
      @include m.mobile {
        font-size: 2rem;
      }
      @include m.laptop {
        font-size: 2.2rem;
      }
      @include m.desktopXL{
        font-size: 2.6rem;
      }
    }
  }
  .lab-pack-grid {
    display: grid;
    grid-template-columns: 1fr;
    .lab-pack-element-odd {
      border-radius: 3rem 0 0 0;
    }
    .lab-pack-element-even {
      border-radius: 3rem 0 0 0;
      @include m.tablet {
        border-radius: 0 3rem 0 0;
      }
    }
    .lab-pack-element-odd,
    .lab-pack-element-even {
      position: relative;
      color: v.$white;
      height: auto;
      background-color: v.$primaryBlueDark;
      display: flex;
      justify-content: center;
      align-items: center;
      margin: 2.5rem auto;
      width: 90%;
      @include m.mobile {
        width: 70%;
      }
      @include m.tablet {
        width: 85%;
      }
      @include m.desktop {
        // width: 27rem;
        width: 100%;
      }
      @include m.desktopXL {
        width: 100%;
      }
    }
    .lab-pack-text {
      padding-top: 2rem;
      padding-bottom: 1rem;
      padding-left: 2rem;
      padding-right: 7rem;
      @include m.tablet {
        height: 11rem;
      }
      @include m.laptop {
        height: 10rem;
      }
      @include m.desktop {
        height: 9rem;
      }
      @include m.desktopXL {
        height: 10rem;
      }
    }
    // .lab-pack-price {
    //   position: absolute;
    //   background-color: v.$grayBackground;
    //   color: v.$primaryBlueDark;
    //   width: 7rem;
    //   height: 7rem;
    //   right: -1rem;
    //   bottom: -3rem;
    //   display: flex;
    //   justify-content: center;
    //   // padding-top: 3rem;
    //   align-items: center;
    //   border-radius: 50% 50% 0 0;
    //   p {
    //     margin: 0;
    //     font-size: 1.5rem;
    //     @include m.tablet {
    //       font-size: 2rem;
    //     }
    //   }
    // }
    @include m.tablet {
      grid-template-columns: repeat(2, 1fr);
      gap: 1rem;
    }
    @include m.desktop {
      column-gap: 5rem;
    }
    @include m.desktopXL {
      column-gap: 6rem;
    }
  }
  .lab-pack-final {
    .lab-pack-element-odd {
      border-radius: 3rem 0 0 0;
    }
    .lab-pack-element-even {
      border-radius: 3rem 0 0 0;
    }
    .lab-pack-element-odd,
    .lab-pack-element-even {
      position: relative;
      color: v.$white;
      height: auto;
      background-color: v.$primaryBlueDark;
      display: flex;
      justify-content: center;
      align-items: center;
      margin: 2.5rem auto;
      width: 95%;
      @include m.mobile {
        width: 70%;
      }
      @include m.tablet {
        width: 45%;
      }
      @include m.desktopXL {
        width: 30%;
      }
      // @include m.laptop {
      //   width: 35%;
      // }
    }
    .lab-pack-text {
      padding-top: 2rem;
      padding-bottom: 1rem;
      padding-left: 2rem;
      padding-right: 7rem;
    }
    // .lab-pack-price {
    //   position: absolute;
    //   background-color: v.$grayBackground;
    //   color: v.$primaryBlueDark;
    //   width: 7rem;
    //   height: 7rem;
    //   right: -1rem;
    //   bottom: -3rem;
    //   display: flex;
    //   justify-content: center;
    //   padding-top: 3rem;
    //   border-radius: 50% 50% 0 0;
    //   p {
    //     font-size: 1.5rem;
    //   }
    // }
    margin-bottom: 7rem;
    @include m.tablet {
      margin-top: 5rem;
    }
  }
  margin: 1rem 1.5rem 3rem 1.5rem;
  @include m.mobile {
    margin: 3rem 1.5rem 3rem 1.5rem;
  }
  @include m.laptop {
    margin: 6rem 7rem 3rem 7rem;
  }
  @include m.desktop {
    margin: 12rem 20rem 3rem 20rem;
  }
  @include m.desktopXL {
    margin: 12rem 30rem 3rem 30rem;
  }
}

.lab-pack-columns {
  margin: 3rem 1.5rem;
  .lab-pack-element {
    margin-top: 3rem;
    border: 1px solid v.$primaryBlueDark;
    border-radius: 0 0 5rem 5rem;
    .lab-pack-column-title {
      background-color: v.$primaryBlueDark;
      color: v.$white;
      text-align: center;
      padding: 2rem;
      p {
        margin: 0;
      }
    }
    .lab-pack-column-text {
      color: v.$secondaryGray;
      padding: 2rem 2rem 1rem 2rem;
      ul {
        li {
          list-style-position: inside;
          &::marker {
            margin-right: 0 !important;
          }
        }
      }
    }
    .lab-pack-column-price {
      color: v.$primaryBlueDark;
      padding: 0 3rem 2rem 3rem;
      text-align: right;
      font-size: 3rem;
    }
    @include m.mobile {
      margin-top: 5rem;
    }
  }
  @include m.mobile {
    margin: 3rem 6rem 7.5rem 6rem;
  }
  @include m.tablet {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 3rem;
    margin: 3rem 3rem 7.5rem 3rem;
  }
  @include m.laptop {
    margin: 3rem 9rem 7.5rem 9rem;
    gap: 4rem;
  }
  @include m.desktopXL {
    margin: 3rem 40rem 7.5rem 40rem;
    gap: 6rem;
  }
}

.ellipsis {
  margin-left: 1rem;
  margin-right: 1rem;
  display: flex;
  align-items: flex-end;
  p {
    font-size: 2.5rem;
    margin: 0;
  }
}

.directory {
  @include m.desktop {
    .reveal {
      position: relative;
      opacity: 0;
    }
    .reveal.active {
      opacity: 1;
    }
    // .active.fade-bottom {
    //   animation: fade-bottom 1s ease-in;
    // }
    .active.fade-left {
      animation: fade-left 1s ease-in;
    }
    .active.fade-right {
      animation: fade-right 1s ease-in;
    }
    // @keyframes fade-bottom {
    //   0% {
    //     transform: translateY(50px);
    //     opacity: 0;
    //   }
    //   100% {
    //     transform: translateY(0);
    //     opacity: 1;
    //   }
    // }
    @keyframes fade-left {
      0% {
        transform: translateX(-100px);
        opacity: 0;
      }
      100% {
        transform: translateX(0);
        opacity: 1;
      }
    }
    @keyframes fade-right {
      0% {
        transform: translateX(100px);
        opacity: 0;
      }
      100% {
        transform: translateX(0);
        opacity: 1;
      }
    }
  }
}