@use "base/variables" as v;
@use "base/mixins" as m;

.fertility-title {
  position: relative;
  display: flex;
  justify-content: center;
  h2 {
    font-family: v.$title;
    position: absolute;
    text-transform: uppercase;
    color: v.$white;
    font-size: 3rem;
    top: -2rem;
    text-align: center;
    @include m.tablet {
      font-size: 4rem;
      top: -2.5rem;
    }
    @include m.desktop {
      font-size: 8rem;
      top: -5rem;
    }
    @include m.desktopXL {
      font-size: 10rem;
      top: -6rem;
    }
  }
}

.fertility-description {
  height: 40rem;
  padding: 5rem 2rem;
  @include m.mobile {
    padding: 7rem 5rem;
  }
  @include m.tablet {
    padding: 7rem 7rem;
  }
  @include m.desktop {
    padding: 12rem 15rem;
  }
  @include m.desktopXL {
    padding: 20rem 15rem;
  }
  background-color: v.$primaryBlueDark;
  p {
    color: v.$white;
    margin: 0;
    // font-size: 1.5rem;
    line-height: 3rem;
    @include m.desktop {
      // font-size: 2rem;
      line-height: 5rem;
    }
    @include m.desktopXL {
      // font-size: 2.5rem;
      line-height: 5rem;
    }
    text-align: center;
    font-family: v.$text;
  }
}

.fertility-fertihaus {
  background-color: v.$pinkText;
  display: grid;
  grid-template-rows: 1fr 2fr;
  margin: auto;
  border-radius: 0;
  height: 32.5rem;
  .fertihaus-logo {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 2.5rem 3rem 2rem 3rem;
    // padding-bottom: 0;
    img {
      width: 100%;
    }
    @include m.mobile {
      padding: 4rem 7.5rem 2rem 7.5rem;
      padding: 3rem 3rem;
      margin-left: 4rem;
      margin-right: 4rem;
      margin-top: 0;
      margin-bottom: 0;
    }
    @include m.tablet {
      border-bottom: 2px solid v.$white;
      padding: 5rem 8.5vw;
      margin: 0;
      margin-left: 2rem;
      margin-right: 2rem;
    }
    @include m.laptop {
      border-bottom: none;
      border-right: 2px solid v.$white;
      padding: 0 3.5rem 0 3.5rem;
      padding-left: 0.5rem;
      padding-right: 3rem;
      margin-top: 2rem;
      margin-bottom: 2rem;
    }
  }
  @include m.mobile {
    height: auto;
    grid-template-rows: 1fr 1fr;
  }
  @include m.tablet {
    // width: 85%;
    width: -webkit-fill-available;
    margin-left: 16vw;
    margin-right: 16vw;
    border-radius: 0 10rem 0 0;
    grid-template-rows: unset;
    margin-bottom: 10rem;
  }
  @include m.laptop {
    grid-template-columns: 1fr 2fr;
    margin-left: 10vw;
    margin-right: 10vw;
  }
  @include m.desktop {
    margin-left: 16vw;
    margin-right: 16vw;
  }
  @include m.desktopXL {
    margin-left: 20vw;
    margin-right: 20vw;
  }
  .fertihaus-info {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    p {
      color: v.$white;
      // font-size: 2rem;
      font-family: v.$text;
      padding: 0 1.5rem 2rem 1.5rem;
      text-align: center;
      @include m.mobile {
        padding: 0 5rem 2rem 5rem;
      }
      @include m.tablet {
        padding: 6rem 5rem 1rem 5rem;
      }
    }
    a {
      @include m.mobile {
        margin-bottom: 4rem;
      }
      @include m.tablet {
        margin-bottom: 5rem;
      }
    }
  }
}

.fertility {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  a {
    margin-top: 3rem;
    margin-bottom: 2rem;
    @include m.mobile {
      margin-top: 0;
      margin-bottom: 5rem;
    }
    @include m.tablet {
      margin-top: 2rem;
    }
    @include m.desktop {
      margin-top: -5rem;
    }
  }
}

.fertihaus-btn {
  button {
    font-size: 1.5rem;
    padding: 0.5rem;
    @include m.mobile {
      font-size: 2rem;
    }
    @include m.tablet {
      font-size: 2.25rem;
    }
    @include m.desktop {
      font-size: 2.5rem;
      padding: 1rem 2rem;
    }
  }
}