.directory-grid {
  margin: 10rem 1rem 6rem 1rem;
  display: grid;
  grid-template-columns: 1fr;
  gap: 3rem;
  @include m.mobile {
    margin: 7rem 4rem;
  }
  @include m.tablet {
    margin: 5rem 2rem;
    grid-template-columns: 1fr 1fr;
  }
  @include m.desktop {
    grid-template-columns: 1fr;
    margin: 10rem auto;
    gap: 5rem;
    width: 90%;
  }
  @include m.desktopXL {
    width: 70%;
  }
  .directory-text {
    .directory-text-align {
      display: flex;
      align-items: center;
    }
    display: grid;
    padding: 2rem 1.5rem;
    @include m.tablet {
      grid-template-rows: 3fr 1fr;
      padding: 2rem 2rem;
    }
    @include m.laptop {
      grid-template-rows: 3fr 1fr;
      padding: 2rem 2.5rem;
    }
    @include m.desktop {
      padding: 2rem 7.5rem;
    }
    @include m.desktopXL {
      grid-template-rows: 3fr 2fr;
    }
    p {
      vertical-align: middle;
      font-family: v.$text;
      color: v.$secondaryGray;
      text-align: center;
      @include m.laptop {
        margin: 1.5rem;
      }
      @include m.desktop {
        font-size: 1.85rem;
      }
      @include m.desktopXL {
        font-size: 2rem;
      }
    }
    .directory-btn {
      display: flex;
      justify-content: center;
      align-items: center;
      // @include m.desktop {
      //   align-items: flex-end;
      // }
      @include m.desktopXL {
        align-items: center;
      }
    }
    // a {
    //   font-family: v.$textNormal;
    //   color: v.$white;
    //   text-decoration: none;
    //   background-color: v.$primaryBlue;
    //   padding: 1rem 1.5rem;
    //   border-radius: 1.75rem;
    //   height: fit-content;
    //   @include m.mobile {
    //     padding: 1rem 3.5rem;
    //   }
    //   transition: all 0.5s;
    //   &:hover {
    //     background-color: darken($color: v.$blueBtn, $amount: 10);
    //     transition: all 0.5s;
    //   }
    // }
  }
  .directory-logo {
    background-color: v.$primaryBlueDark;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: v.$white;
    font-family: v.$text;
    text-align: center;
    font-size: 1.25rem;
    padding: 1rem 0;
    @include m.tablet {
      font-size: 1.7rem;
      padding: 1.5rem 0;
    }
    @include m.desktop {
      font-size: 2rem;
      padding: 1.5rem 0;
    }
    @include m.desktopXL {
      font-size: 3rem;
      padding: 1.5rem 0;
    }
    .img-container {
      height: 7rem;
      margin-top: 0rem;
      margin-bottom: 0rem;
      @include m.tablet {
        height: 9rem;
        margin-top: 1rem;
        margin-bottom: 1rem;
      }
      @include m.laptop {
        height: 11rem;
        margin-top: 0rem;
        margin-bottom: 0rem;
      }
      @include m.desktop {
        height: 10rem;
        margin-top: 2rem;
      }
      @include m.desktopXL {
        height: 12rem;
        margin-top: 1rem;
      }
      img {
        height: 100%;
      }
    }
    p {
      font-family: v.$title;
      @include m.mobile {
        padding-left: 1rem;
        padding-right: 1rem;
      }
      @include m.desktop {
        font-size: 2rem;
      }
      @include m.desktopXL {
        padding-left: 2rem;
        padding-right: 2rem;
        font-size: 2.5rem;
      }
    }
  }
  .directory-text {
    background-color: v.$grayBackground;
  }
  .directory-odd {
    display: grid;
    grid-template-rows: 1fr 2fr;
    @include m.mobile {
      grid-template-rows: 2fr 3fr;
    }
    @include m.desktop {
      grid-template-columns: 1fr 4fr;
      grid-template-rows: 1fr;
      gap: 3rem;
    }
    .directory-logo {
      border-radius: 5rem 5rem 0 0;
      @include m.desktop {
        border-radius: 10rem 0 0 0;
      }
    }
    .directory-text {
      border-radius: 0 0 0 0;
      @include m.desktop {
        border-radius: 0 0 10rem 0;
      }
    }
  }
  .directory-even {
    display: grid;
    grid-template-rows: 1fr 2fr;
    @include m.mobile {
      grid-template-rows: 2fr 3fr;
    }
    @include m.desktop {
      grid-template-columns: 4fr 1fr;
      grid-template-rows: 1fr;
      gap: 3rem;
    }
    .directory-logo {
      order: -1;
      border-radius: 5rem 5rem 0 0;
      @include m.desktop {
        order: 0;
        border-radius: 0 10rem 0 0;
      }
    }
    .directory-text {
      border-radius: 0 0 0 0;
      @include m.desktop {
        border-radius: 0 0 0 10rem;
      }
    }
  }
}

.service {
  margin-top: 2rem;
  margin-bottom: 2rem;
  // margin: 5rem;
  // height: 150vw;
  @include m.tablet {
    margin-top: 8rem;
    margin-bottom: 5rem;
    // height: 15rem;
    // margin-top: 5rem;
  }
  @include m.laptop {
    // height: 20rem;
    margin-top: 15rem;
    margin-bottom: 10rem;
  }
  @include m.desktop {
    // height: 30rem;
    margin-top: 20rem;
  }
  .service-head {
    // position: relative;
    h2 {
      text-transform: uppercase;
      // position: absolute;
      // top: 1rem;
      font-family: v.$titleBold;
      color: v.$primaryBlueDark;
      font-size: 2rem;
      text-align: center;
      width: 100%;
      @include m.tablet {
        width: 70%;
        font-size: 2.5rem;
        margin-left: 19vw;
        text-align: left;
      }
      @include m.laptop {
        width: 60%;
        font-size: 3.5rem;
        margin-left: 21vw;
      }
      @include m.desktop {
        width: 60%;
        margin-left: 19vw;
        font-size: 5rem;
      }
      @include m.desktopXL {
        // margin-left: 20%;
        // font-size: 5rem;
        margin-left: 22vw;
      }
      .head-1 {
        position: relative;
        .head-2 {
          position: absolute;
          color: v.$white;
          width: 200%;
          left: 100%;
          transform: translate(-75%, 2.5rem);
          @include m.tablet {
            left: 0;
            transform: unset;
            width: 70rem;
            top: 3.5rem;
          }
          @include m.desktop {
            top: 5.5rem;
          }
        }
      }
    }
    .bottom-area {
      color: v.$white;
      z-index: -1;
      height: auto;
      // width: -webkit-fill-available;
      // margin-left: 16vw;
      // margin-right: 16vw;
      background-color: v.$primaryBlueDark;
      position: relative;
      top: -0.5rem;
      // top: 3.5rem;
      border-radius: 0 0 0 0;
      padding: 10% 2rem;
      text-align: center;
      @include m.tablet {
        padding: 6.5rem 2rem 2rem 2rem;
        // width: 85%;
        top: -1rem;
        // margin-left: 3rem;
        width: -webkit-fill-available;
        margin-left: 16vw;
        margin-right: 16vw;
        border-radius: 0 0 0 5rem;
        text-align: justify;
      }
      @include m.laptop {
        // width: 72.5%;
        margin-left: 19vw;
        margin-right: 19vw;
        // top: 4.5rem;
      }
      @include m.desktop {
        // width: 80%;
        // top: 6.5rem;
        margin-left: 16vw;
        margin-right: 16vw;
        border-radius: 0 0 0 10rem;
        padding: 12.5rem 4rem 4rem 4rem;
      }
      @include m.desktopXL {
        // width: 70%;
        // top: 6.5rem;
        // margin-left: 15%;
        margin-left: 20vw;
        margin-right: 20vw;
      }
      ul {
        li {
          font-family: v.$text;
          list-style: disc;
          list-style-position: inside;
          margin-left: 2rem;
        }
      }
      p {
        font-family: v.$text;
      }
    }
    .service-head-logo {
      display: none;
      position: absolute;
      right: -5%;
      top: -7.5rem;
      // padding: 2rem;
      background-color: v.$grayBackground;
      width: 25rem;
      height: 25rem;
      border-radius: 50% 50% 0 0;
      @include m.tablet {
        // padding-top: 1rem;
        display: flex;
        width: 12.5rem;
        height: 12.5rem;
        justify-content: center;
        align-items: center;
        // top: -5vw;
        // right: 11vw;
        // top: -2rem;
        // right: 3rem;
      }
      @include m.laptop {
        top: -10.5rem;
        width: 15.5rem;
        height: 15.5rem;
        // right: 10rem;
      }
      @include m.desktop {
        top: -17rem;
        width: 23rem;
        height: 23rem;
        // right: 10rem;
      }
      @include m.desktopXL {
        top: -19.5rem;
        right: -2.5%;
        width: 28rem;
        height: 28rem;
        // right: 25%;
        // padding: 1rem 2rem 5rem 2rem;
      }
      img {
        height: 100%;
        @include m.tablet {
          // padding: 0 0.5rem 1rem 0.5rem;
        }
        @include m.laptop {
          // padding: 0 1rem 2rem 1rem;
        }
        @include m.desktop {
          // padding: 4rem 4rem 1rem 4rem;
        }
      }
    }
  }
}

