.service-gallery {
  background-color: v.$white;
  width: 80%;
  height: auto;
  display: grid;
  grid-template-columns: 1fr;
  gap: 2rem;
  max-width: 120rem;
  margin: 2rem auto 2rem auto;
  @include m.mobile {
    grid-template-columns: repeat(1, 1fr);
    margin: 0 3rem 4rem 3rem;
  }
  @include m.tablet {
    grid-template-columns: repeat(3, 1fr);
    margin-bottom: 7.5rem;
    margin-left: auto;
    margin-right: auto;
  }
  @include m.laptop {
    margin-bottom: 10rem;
  }
  .service-img {
    width: 100%;
    border: 1rem solid v.$grayBackground;
    img {
      width: 100%;
    }
  }
}
